import styles from './Screen2.module.css';
import {setActiveSlide} from "../../../store/reducers/Swiper";
import {useDispatch} from "react-redux";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import {useEffect, useState} from "react";
import './data.css';
import TimeField from "react-simple-timefield";
import {dataStart, dataTime} from "../../../parsing/data";
import {ButtonFurther} from "../../../universal/ButtonFurther/ButtonFurther";

export const Screen2 = () => {

  const dispatch = useDispatch();
  const [selectedDate, setSelectedDate] = useState(null);
  const [parsData, setParsData] = useState(dataStart());

  useEffect(() => {
    selectedDate && setParsData(dataTime(selectedDate))
  }, [selectedDate]);

  const [time, setTime] = useState("10:00");

  const handleChange = (event) => {
    setTime(event.target.value);
  };


  return (
    <div className={styles.container}>
      <p>Date {parsData}</p>
      <DatePicker
        selected={selectedDate}
        onChange={(date) => setSelectedDate(date)}
        style={{color: 'red', backgroundColor: 'lightgray', border: '1px solid black'}}
        inline={true}
        openToDate={new Date()}
      />
      <p>Time {parsData}</p>
      <TimeField
        style={styleTimeField}
        value={time}
        onChange={handleChange}
        input={<input type="text" />}
      />
      <div className={styles.bottomBtns}>
        <ButtonFurther
          title={'Back'}
          callback={() => dispatch(setActiveSlide(0))}
          className={styles.continue}
        />
        <ButtonFurther
          // disabled={activeBtn_1 === '' || activeBtn_2 === ''}
          title={'Continue '}
          callback={() => dispatch(setActiveSlide(2))}
          className={styles.continue}
        />
      </div>
    </div>
  )
};

const styleTimeField = {
  backgroundColor: 'none',
  borderRadius: '8px',
  padding: '10px 5px',
  width: 'min-content',
  border: '2px solid #d0b386',
  color: '#d0b386'
}